import { authAxios, StoreData } from "..";

export const initServices = (storeData: StoreData) => {
  let statisticsApi = "/api/store_info/statistics";
  let ordersApi = "/api/store_info/orders";
  if (storeData.accessToken) {
    statisticsApi = "/api/store/plug/woocommerce/statistics_info";
    ordersApi = "/api/store/plug/woocommerce/order/list";
  }

  return {
    orders: (pageNum: number, pageSize: number) =>
      authAxios({
        url: ordersApi,
        method: "GET",
        params: { pageNum, pageSize },
      }),
    statistics: () =>
      authAxios({
        url: statisticsApi,
        method: "GET",
      }),
    updateDomain: () =>
      authAxios({
        url: "/api/store/plug/woocommerce/change_domain",
        method: "GET",
        params: { domain: storeData.website },
      }),
  };
};
