import React, { useState, useEffect } from "react";
import { services } from "@/index";
import styles from "./styles.module.css";
import { Modal, Table } from "antd";
import { ColumnsType } from "antd/lib/table";

export const List = () => {
  const [list, setList] = useState<any[]>([]);
  const [pageNum, setPageNum] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    services.orders(pageNum, pageSize).then((response) => {
      if (response.data.code === "100000") {
        setList(response.data.data.result);
        setTotal(response.data.data.total);
      } else {
        Modal.error({ mask: false, content: response.data.msg });
      }
    });
  }, [pageNum, pageSize]);

  const columns: ColumnsType<any> = [
    {
      title: "Order ID",
      dataIndex: "orderSn",
    },
    {
      title: "Date",
      dataIndex: "gmtThirdOrder",
    },
    {
      title: "Status",
      dataIndex: "orderStatus",
    },
    {
      title: "Total",
      dataIndex: "money",
    },
  ];

  const onChange = (page: number, pageSize?: number | undefined) => {
    if (page) setPageNum(page);
    if (pageSize) setPageSize(pageSize);
  };

  const onShowSizeChange = (current: number, size: number) => {
    if (current) setPageNum(current);
    if (size) setPageSize(size);
  };
  return (
    <div className={styles.list}>
      <span className={styles.title}>Yoycol Orders</span>
      <Table<any>
        rowKey="orderSn"
        columns={columns}
        dataSource={list}
        pagination={{
          current: pageNum,
          pageSize,
          total,
          onChange,
          onShowSizeChange,
        }}
      />
    </div>
  );
};
