import React, { useEffect, useState } from "react";
import Dashboard from "@/components/Dashboard";
import { Connect } from "@/components/Connect/Connect";
import { services, StoreData } from "@/index";
import styles from "./styles.module.css";
import "./App.less";
import { Alert, Divider, notification } from "antd";

interface PropTypes {
  storeData: StoreData;
}

// const { TabPane } = Tabs;

export const App = ({ storeData }: PropTypes) => {
  // useEffect(() => {
  //   serviceOptions.axios = axios.create({
  //     baseURL: storeData.apiHost,
  //     headers: { Authorization: storeData.accessToken || storeData.accessKey },
  //   });
  // }, [storeData]);
  const [message, setMessage] = useState<any>(null);
  useEffect(() => {
    if (storeData.updateDomain) {
      // 更新域名
      services.updateDomain();
    }
  }, [storeData]);
  const url = storeData
    ? storeData.apiHost +
      storeData.woocommerceConnect +
      "?storeName=" +
      encodeURIComponent(storeData.name) +
      "&domain=" +
      encodeURIComponent(storeData.website) +
      "&salt=" +
      storeData.salt
    : "";
  return (
    <div className={styles.app}>
      {/* <Tabs>
        <TabPane tab="Dashboard" key="Dashboard">
          <Dashboard />
        </TabPane>
        <TabPane tab="Settings" key="Settings">
          <Settings />
        </TabPane>
        <TabPane tab="Support" key="Support">
          <Support />
        </TabPane>
      </Tabs> */}
      {message && (
        <>
          <Alert {...message} />
          <Divider />
        </>
      )}
      {storeData.accessKey || storeData.accessToken ? (
        <Dashboard />
      ) : (
        <Connect url={url} />
      )}
    </div>
  );
};
