import React from "react";
import ReactDOM from "react-dom";
import axios from "axios";
import "./index.css";
import { App } from "./components/App";
import * as serviceWorker from "./serviceWorker";
import { initServices } from "./service";

export interface StoreData {
  website: string;
  version: string;
  name: string;
  apiHost: string;
  salt: string;
  accessKey: string;
  accessToken: string;
  storeId: string;
  woocommerceConnect: string;
  updateDomain: boolean;
}

const urlParams = new URLSearchParams(window.location.search);
const dataJSON = urlParams.get("yoycol-data") || "";
console.log(dataJSON);
if (dataJSON.length > 0) {
  (window as any).yoycolStoreData = JSON.parse(decodeURIComponent(dataJSON));
}

const storeData: StoreData = (window as any).yoycolStoreData as StoreData;
let _authAxios = axios.create();
if (storeData) {
  _authAxios = axios.create({
    baseURL: storeData.apiHost,
    headers: {
      token: storeData.accessKey || storeData.accessToken,
      storeId: storeData.storeId,
    },
  });
}

export const authAxios = _authAxios;

export const services = initServices(storeData);

ReactDOM.render(
  <React.StrictMode>
    <App storeData={storeData || {}} />
  </React.StrictMode>,
  document.getElementById("yoycol-root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
